
import Header from "./Header.vue";
import {useI18n} from "vue-i18n";
import {defineComponent} from "vue";
export default defineComponent({
  name: 'StartSection',
  components: {Header},
  props: {
    msg: String
  },
  setup() {
    const {t, locale} = useI18n();
    return {t, locale}
  }
})
