
import Footer from '@/components/Footer.vue'
import Header from "@/components/Header.vue";
import {useI18n} from "vue-i18n";
import {defineComponent} from "vue";
import ScrollReveal from "scrollreveal";

export default defineComponent({
  components: {Header, Footer },
  name: 'ProductPage',
  setup() {
    const {t, locale} = useI18n();
    return {t, locale}
  },
  mounted(){
          // Scroll reveal script to animate when entering and leaving viewport

    const revealFromRight = {
        reset:true,
        origin : 'right',
        distance : '120px',
        easing   : 'ease-out',
    };
    const revealFromLeft = {
        reset:true,
        duration: 500,
        origin : 'left',
        distance : '300px',
        easing   : 'cubic-bezier(.38,.3,.62,1.47)',
    };
    const revealFromLeftFast = {
        reset:true,
        duration: 350,
        origin : 'left',
        distance : '400px',
        easing   : 'cubic-bezier(.38,.3,.62,1.47)',
    };
    const revealFromLeftFast2 = {
        delay: 400,
        reset:true,
        duration: 350,
        origin : 'left',
        distance : '400px',
        easing   : 'cubic-bezier(.38,.3,.62,1.47)',
    };

    const revealFromLeftDelay1 = {
        delay: 400,
        duration: 500,
        reset:true,
        useDelay: 'onload',
        origin : 'left',
        distance : '300px',
        easing   : 'cubic-bezier(.38,.3,.62,1.47)',
    }
    const revealFromLeftDelay2 = {
        delay: 800,
        duration: 500,
        reset:true,
        origin : 'left',
        distance : '300px',
        easing   : 'cubic-bezier(.38,.3,.62,1.47)',
    }


    ScrollReveal().reveal('.dashboard-image', revealFromRight);
    ScrollReveal().reveal('.homeImage-c1', revealFromLeft);
    ScrollReveal().reveal('.homeImage-c2', revealFromLeftDelay1);
    ScrollReveal().reveal('.homeImage-c3', revealFromLeftDelay2);
    ScrollReveal().reveal('.installmentImage-c1', revealFromLeftFast);
    ScrollReveal().reveal('.installmentImage-c2', revealFromLeftFast2);
    ScrollReveal().reveal('.installmentImage-c3', revealFromLeftFast);
  }
})
